body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
		"Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.header {
	font-family: "Calligraffitti", cursive;
	text-shadow: -15px 5px 20px #ced0d3;
	letter-spacing: 0.01em;
	font-size: 4rem;
	text-align: center;
	color: #f9f1cc;
	text-shadow: 5px 5px 0px #ffb650, 10px 10px 0px #ffd662, 15px 15px 0px #ff80bf,
		20px 20px 0px #6868ac;
	margin-bottom: 2%;
}

.capitals .countries {
	width: 100%;
}

.countries {
	margin-bottom: 5%;
}
button {
	font-size: 20px;
	margin: 2%;
	box-sizing: border-box;
	padding: 5px 10px;
	text-transform: uppercase;
	background-color: white;
	border: 4px solid rgb(209, 204, 204);
	cursor: pointer;
	border-radius: 15%;
}

button:hover {
	background-color: #cecee4;
	border-color: #cecee4;
}
.congratulation {
	font-family: "Calligraffitti", cursive;
	font-weight: 700;
	font-size: 8rem;
	position: absolute;
	text-shadow: -15px 5px 20px #ced0d3;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	letter-spacing: 0.01em;
	text-align: center;
	color: #f9f1cc;
	text-shadow: 5px 5px 0px #ffb650, 10px 10px 0px #ffd662, 15px 15px 0px #ff80bf,
		20px 20px 0px #ef5097, 25px 25px 0px #6868ac, 30px 30px 0px #90b1e0;
	margin: 0;
	position: absolute;
	animation: grow 2s linear;
}

@keyframes grow {
	0% {
		font-size: 6rem;
	}
	100% {
		font-size: 8rem;
	}
}
